<template>
		<view class="cu-modal " :class="ModalName" @tap="hideModal">
			<view class="cu-dialog" @tap.stop="">
				<view class="cu-bar bg-white justify-end">
					<view class="content text-bold text-cut">{{ isEdit ? '编辑部门':'添加部门' }}</view>
					<view class="action" @tap="hideModal">
						<text class="cuIcon-close text-red"></text>
					</view>
				</view>
				<uni-forms  ref="form" :value="current" validate-trigger="bind" err-show-type="undertext">
					<uni-forms-item name="orgNo">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">编码</view>
							<input placeholder="请输入部门编码" :value="current.orgNo"  @input="changeOrgNo($event);binddata('orgNo',$event.detail.value ,'form')"/>
						</view>
					</uni-forms-item>
					<uni-forms-item name="name">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">名称</view>
							<input placeholder="请输入部门名称" :value="current.name"  @input="changeName($event);binddata('name',$event.detail.value ,'form')"/>
						</view>
					</uni-forms-item>
					<uni-forms-item name="phone">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">联系电话</view>
							<input placeholder="请输入部门联系电话" v-model="current.phone" />
						</view>
					</uni-forms-item>
					
					<uni-forms-item name="postCode">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">邮编</view>
							<input placeholder="请输入邮政编码" v-model="current.postCode" />
						</view>
					</uni-forms-item>
					<uni-forms-item name="address">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">地址</view>
							<textarea placeholder="请输入地址" v-model="current.address"></textarea>
						</view>
					</uni-forms-item>
					<uni-forms-item name="managers">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">负责人</view>
							<picker @change="changeMangers" :value="managersIndex"  :range="staff_list" range-key="Name">
							     <view class="uni-input">{{staff_list[managersIndex] ? staff_list[managersIndex].Name : '未选择'}}</view>
							 </picker>
						</view>
					</uni-forms-item>
					<uni-forms-item name="parentId">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">上级部门</view>
							<uni-data-picker placeholder="请选择上级部门" style="width: 75%;" :preload="true"
								:localdata="organtrees" popup-title="部门"  @nodeclick="changeParentId" :value="current.parentId" >
							</uni-data-picker>
						</view>
					</uni-forms-item>
					
					<uni-forms-item name="sort">
						<view class="cu-form-group solid-bottom">
							<view class="title text-bold">排序</view>
							<uni-number-box min="0" v-model="current.sort"></uni-number-box>
						</view>
					</uni-forms-item>
				</uni-forms>
				
				<view class="cu-bar bg-blue" @tap="save('form')">
					<view class="action  margin-0 flex-sub  solid-left">{{ isEdit ? '编辑': '添加' }}</view>
				</view>
			</view>
		</view>
</template>

<script>
	/**
	 * 添加部门或编辑部门
	 * @author 陆彦捷
	 * @date 2021.06.24 添加部门
	 * @date 2021.06.29 添加部门/编辑部门
	 * @date 2021.07.23 重新应用login
	 * @description  添加当前部门/编辑部门
	 */
	import department from '@/common/request/repairorder.js';	// 部门
	import organ from '@/common/storage/organstorage.js';			// 组织
	import staff from '@/common/request/staff.js';			// 员工
	
	import changeobj from '@/common/function/changeobjectecordname.js'; // 格式化值
	import filterobj from '@/common/function/filterobject.js';			// 过滤obj值
	
	
	var _self;
	export default {
		name:"organ-department",
		props:{
			// 当前组织id
			MchId : {
				type : Number,
				default: 0
			},
			// 上级部门Id
			parentId :{
				type : Number,
				default: 0
			},
			// 部门id
			orgId : {
				type: Number,
				default:0
			},
			// 是否属于编辑
			isEdit :{
				type: Boolean,
				default: false
			},
			// 刷新数据的方法
			refresh :{
				type: Function,
				default: null
			},
			// 类型
			type:{
				type:String,
				default:''
			},
			// 值
			value :{
				type:Number,
				default:0
			}
		},
		data() {
			return {
				// 与样式有关参数
				ModalName : null ,				// 当前组织
				// 与系统有关参数
				current:{
					  orgId: this.isEdit ? this.orgId : 0,  // 部门id
					  mchId:  this.MchId,		// 组织Id
					  orgNo: "",				// 部门编码
					  name: "",					// 部门名称
					  phone: ""	,				// 部门联系电话
					  managers: [-1],			// 负责人(数组、对应员工id)
					  postCode: "",				// 邮编
					  address: "",				// 地址信息
					  parentId: this.parentId,	// 上级部门
					  sort: 0,					// 排序
				},	
				rules:{
					orgNo:{
						rules:[
							{
								required: true,
								errorMessage: '请输入部门编码'
							},						
						]
					},
					name:{
						rules:[
							{
								required: true,
								errorMessage: '请输入名称'
							},						
						]
					},
				},			// 验证规则
				managersIndex : -1 ,		// 选择的id
				staff_list : [],			// 员工信息(用于选择部门负责人)
				organtrees : [],			// 当前组织下的部门树形图		
			};
		},
		onReady: function() {
			// 初始化表单验证
			this.$refs.form.setRules(this.rules);
		},
		created:function(){
			_self = this;
			// 输出弹出对话框方法
			this.ModalName = 'show';
			this.$emit('show', this.showModal);
			this.init_request();			// 初始化请求
		},
		methods: {
			// ================================================ 初始化 =======================================================
			/**
			 * 初始化请求
			 */
			init_request : function(){
				// 获取员工信息
				staff.staffByMchId_request(this.MchId, null ,
				 function(data){
					// 请求成功时执行方法
					_self.staff_list = result.data.Data;
				 },null,null);
				// 请求部门树形图
				department.organstree_request(this.MchId , null , function(data){
					// datacom化 和过滤当前部门
					var templist = changeobj.change(result.data.Data.Children , "Value" ,"Name","Children");
					// 过滤obj值
					_self.organtrees = filterobj.filterbyvalue_list(templist , _self.orgId);
					// 添加无
					var temp = {
						"value" : 0 ,
						"text" : "无"
					};
					_self.organtrees.unshift(temp);
				}, null ,null);
				// 获取当前部门信息
				if(this.orgId){
					department.organzationByOrgId_request(OrgId , function(){
						// 请求前方法
						uni.showLoading();
					}, function(data){
						// 请求成功执行方法
						_self.currentMapping(data);			// 将获取到的值映射到请求
					},function(errors){
						// 请求失败执行方法
						var temp = '系统错误！';
						if(Boolean(errors.statusCode)){
							temp = errors.data.ResultMsg;	// 返回错误信息
						}
						uni.showToast({
							title: temp,
							icon:'none'
						})
					},null);
				}
			},
			// ================================================ 监听事件 =====================================================
			/** 显示弹出框
			 * @param {Object} e
			 */
			showModal : function (e){
				this.ModalName = 'show';		// 显示弹出框
			},
			/** 隐藏弹出框
			 * @param {Object} e
			 */
			hideModal : function(e){
				// 隐藏hide
				this.ModalName = '';
			},
			/** 修改部门编码
			 * @param {Object} e
			 */
			changeOrgNo : function(e){
				this.current.orgNo = e.detail.value;
			},
			/** 修改名称
			 * @param {Object} e
			 */
			changeName : function(e){
				this.current.name = e.detail.value;
			},
			/** 修改部门联系人
			 * @param {Object} e
			 */
			changeMangers : function(e){
				if(e.detail.value >= 0){
					this.managersIndex = e.detail.value;
					try{
						this.current.managers= [ parseInt(this.staff_list[e.detail.value].StaffId)  ];
					}catch(e){
						this.current.managers = null;
					}
					return;
				}
				this.managersIndex = -1;
				this.current.managers = null ;		//返回部门负责人id
			},
			/** 修改上级部门
			 * @param {Object} e
			 */
			changeParentId : function(item){
				this.current.parentId = item.value;
			},
			/** 添加/编辑部门请求
			 * @param {String} form 表单名称
			 */
			save(form){
				this.$refs[form]
					.submit()
					.then(res => {
						var func = _self.isEdit ? department.organizationUpdate_request : organizationAdd_request;
						func(_self.current , function(){
							// 请求前执行方法
							uni.showLoading();
						},
						function(data){
							// 请求成功后执行方法
							uni.hideLoading();
							uni.showToast({
								title: _self.isEdit ? '更新':'添加',
							});
							setTimeout(function(){
								if(_self.refresh != null){
									_self.refresh(_self.type , _self.value);
								}
							} , 1000);
						},function(errors){
							// 请求失败后执行方法
							uni.hideLoading();
							var temp = '系统错误';
							if(Boolean(errors.statusCode)){
								temp = errors.data.ResultMsg
							}
							uni.showToast({
								title: temp,
								icon:'none'
							});
						}, function(){
							// 请求回调函数
							_self.ModalName = null;
						});
					})
					.catch(errors => {
						uni.showToast({
							title: errors[0].errorMessage,
							icon:'none'
						});
					});
			},
			// ================================================ 公共方法 ========================================
			/** 将获取的值映射到current
			 * @param {Object} data 获取的data数据
			 */
			currentMapping : function(data){
				this.current.orgId = data.OrgId;
				this.current.mchId = data.MachId;
				this.current.orgNo = data.OrgNo;
				this.current.name = data.Name;
				this.current.phone = data.Phone;
				this.current.managers = data.Managers ;
				this.current.postCode = data.PostCode;
				this.current.address = data.Address;
				this.current.parentId = data.ParentId;
				this.current.sort = data.Sort;
			},
			/** 获取部门信息
			 * @param {Object} OrgId 部门Id
			 */
			getOrganization: function(OrgId){
				
			},
			// ==================================================================================================
		}
	}
</script>

<style>

</style>
